<script>
  import input_json_data from '../assets/JSON_Data/input_data.json'
  import input_json_cities from '../assets/JSON_Data/input_cities.json'
  import input_json_calculation from '../assets/JSON_Data/input_calculation_data.json'
  import input_json_graph from '../assets/JSON_Data/input_graph_data.json'
  import input_json_geodata from '../assets/JSON_Data/input_geodata.json'

  import HotTable from 'vue-handsontable-official'
  import {
    LMap,
    LTileLayer,
    LMarker,
    LTooltip,
  } from 'vue2-leaflet';

  function pushTwoDimensionalArray(array1, array2, axis) {
    if (axis != 1) axis = 0;
    if (axis == 0) { // 縦方向の追加
      for (var i = 0; i < array2.length; i++) {
        array1.push(array2[i]);
      }
    } else { // 横方向の追加
      for (var j = 0; j < array1.length; j++) {
        Array.prototype.push.apply(array1[j], array2[j]);
      }
    }
    return array1;
  }

  /*有効数字3桁 method */
  function round3(x) {
    return Math.floor((Number(x) * 1000)) / 1000;
  }

  /*大小比較 method */
  function MaxSort(Max_col, col) {
    for (var i = 0; i < Max_col.length; i++) {
      if (col[i] > Max_col[i]) {
        Max_col[i] = col[i];
      }
    }
    return Max_col;
  }

  function MinSort(Min_col, col) {
    for (var i = 0; i < Min_col.length; i++) {
      if (col[i] < Min_col[i]) {
        Min_col[i] = col[i];
      }
    }
    return Min_col;
  }


  /* Get geomean calculation from 1D array method */
  const transpose = a => a[0].map((_, c) => a.map(r => r[c]));

  /* Get geomean calculation from 1D array method */
  var gmean = require('compute-gmean')

  /* Get column from table method */
  function getCol(matrix, col) {
    var column = [];
    for (var i = 0; i < matrix.length; i++) {
      column.push(matrix[i][col]);
    }
    return column;
  }

  /* Get average from array */
  var average = function (arr) {
    if (typeof arr !== 'object' || arr.length === 0) return false;

    var key, totalNumber = 0;
    for (key in arr) totalNumber = totalNumber + Number(arr[key]);

    return totalNumber / arr.length;
  };

  /* Get (value-max)/(min-max) */
  function Emission(value, max, min) {
    return (value - max) / (min - max);
  }

  /* Get (value-min)/(max-min) */
  function Emission2(value, max, min) {
    return (value - min) / (max - min);
  }

  /* Geomean calc */
  function Geomean(array) {
    var d = 0;
    var output = 1;
    for (var i = 0; i < array.length; i++) {
      if (array[i] != 0) {
        d++;
        output = output * array[i];
      }
    }
    output = output ** (1 / d)
    if (isNaN(output)) {
      output = 0;
    }
    return output
  }

  /* Get Social Welfare method (HDI * Health Index)*0.5 */
  function Social_Welfare(HDI, Health_index) {
    return (HDI * Health_index) * 0.5;
  }

  /* Get USDI method */
  function USDI_calc(EaC, CP, Economic, SW) {
    return ((EaC + CP + Economic + SW) * 0.25);
  }

  export default {
    components: {
      HotTable,
      LMap,
      LTileLayer,
      LMarker,
      LTooltip,
    },
    data: function () {
      return {
        input_data: input_json_data,
        input_cities: input_json_cities,
        input_calculation: input_json_calculation,
        input_graph: input_json_graph,
        input_geodata: input_json_geodata,
        table: 0,
        selected_city: "",
        selected_data: input_json_data,
        selected_city_geodata: ["Tokyo", 35.689487, 139.691711],
        selected_city_column: ["Tokyo", 60.255, 17, 22, 10, 420.7853232, 100, 0.94, 100, 163.8184606, 68776, 112000,
          3.6, 0.979753846, 94.79932331, 0.841591111
        ],
        matrix: [],
        before_matrix: [],
        after_matrix: [],
        merged_matrix: [],
        check1: [],
        check2: [],
        check3: [],
        isShow: false,
        max_column: [255.304, 68.4, 286, 152.60333, 7306.137216, 100, 0.941906005, 100, 224.9, 84700, 125000, 13.9,
          0.979753846, 94.79932331, 0.92
        ],
        min_column: [4.8, 10.2, 9, 5, 16.8, 0.8, 0.003460297, 37, 2, 3425, 8900, 2.2, 0.743415385, 18.87109023,
          0.517804111
        ],
        avarage_column: [59.1518, 34.43, 71.4, 35.660333, 1272.263913, 84.28, 0.293885717, 79.2, 43.02535734,
          24064.001, 50640, 5.73, 0.855621538, 67.64942481, 0.688734784
        ],

        attribution: "<a href='https://maps.gsi.go.jp/development/ichiran.html' target='_blank'>地理院タイル</a>",
        url: 'https://cyberjapandata.gsi.go.jp/xyz/pale/{z}/{x}/{y}.png',
        zoom: 3,

        radar_series: [{
          name: 'Tokyo',
          data: [0.90, 0.92, 0.86, 0.93],
        }, {
          name: 'Average cities',
          data: [0.77, 0.46, 0.46, 0.55],
        }],
        radar_chartOptions: {
          labels: ['Energy and Climate', 'City Plannig', 'Economic', 'Social Welfare'],
          title: {
            text: ''
          },
          dropShadow: {
            enabled: true,
            blur: 1,
            left: 1,
            top: 1
          },
          yaxis: {
            show: true,
            showAlways: true,
            tickAmount: 5,
            min: 0,
            max: 1.0,
            forceNiceScale: false,
            floating: false,
          }
        },
      }
    },
    beforeCreate() {
      this.selected_city = "Tokyo";
    },
    methods: {
      reset_input_data: function () {
        location.reload();
      },
      select_city: function (event) {
        this.selected_city = event.target.value;
        this.selected_city_column = this.$refs.input_data_ref.table.getDataAtCol(2 + this.input_cities.findIndex(e =>
          e === event
          .target.value));
        this.selected_city_geodata = this.$refs.input_geodata_ref.table.getDataAtCol(this.input_cities.findIndex(e =>
          e === event
          .target.value));
        this.radar_series[0].name = event.target.value
        this.radar_series[0].data = this.$refs.input_graph_ref.table.getDataAtCol(this.input_cities.findIndex(e =>
          e === event
          .target.value));
        this.radar_series[0].data = this.radar_series[0].data.slice(1, 5).map(function (value) {
          return String(round3(value));
        });
      },
      Update: function () {
        var col_num = this.$refs.input_data_ref.table.getData()[0].length;
        var row_num = this.$refs.input_data_ref.table.getData().length;

        //input dataを転置
        this.matrix = transpose(this.input_data);
        //選択した都市のデータを抽出
        this.matrix = this.matrix[(2 + this.input_cities.findIndex(e =>
          e === this.selected_city_column[0]))];

        //更新した値を最大値と比較して、更新
        this.max_column = MaxSort(this.max_column, this.selected_city_column.slice(1, this.selected_city_column
          .length));
        this.min_column = MinSort(this.min_column, this.selected_city_column.slice(1, this.selected_city_column
          .length));

        //前後の２次元配列を抽出
        this.before_matrix = this.$refs.input_data_ref.table.getData(0, 0, row_num - 1, 1 + (this.input_cities
          .findIndex(e =>
            e === this.selected_city_column[0])));
        this.after_matrix = this.$refs.input_data_ref.table.getData(0, 3 + (this.input_cities.findIndex(e =>
          e === this.selected_city_column[0])), row_num - 1, col_num - 3);
        //前後のデータ,Max,Minと更新された都市の数値を行列として結合
        this.merged_matrix = pushTwoDimensionalArray(this.before_matrix, transpose([this.selected_city_column.map(
          function (value) {
            //配列の各要素をStringにする
            return String(value);
          })]), 1);
        this.merged_matrix = pushTwoDimensionalArray(this.merged_matrix, this.after_matrix, 1);

        //平均値、最大値、最小値の更新
        var average_array = [];
        var max_array = [];
        var min_array = [];
        for (var i = 1; i < row_num; i++) {
          average_array.push(average(this.merged_matrix[i].slice(2, col_num - 2)));
          max_array.push(Math.max.apply(null, this.merged_matrix[i].slice(2, col_num - 2)));
          min_array.push(Math.min.apply(null, this.merged_matrix[i].slice(2, col_num - 2)));
        }

        //テーブルに反映
        var array2 = [];
        for (var j = 2; j < col_num - 2; j++) {
          array2.push(transpose(this.merged_matrix)[j]);
        }

        this.merged_matrix = pushTwoDimensionalArray(
          pushTwoDimensionalArray(transpose([transpose(this.merged_matrix)[0]]), transpose([
            ["Average 22 cities"].concat(average_array.map(function (element) {
              return String(element);
            }))
          ]), 1),
          transpose(array2), 1);

        this.merged_matrix = pushTwoDimensionalArray(this.merged_matrix, transpose([
          ["Max"].concat(max_array.map(function (element) {
            return String(element);
          }))
        ]), 1);
        this.merged_matrix = pushTwoDimensionalArray(this.merged_matrix, transpose([
          ["Min"].concat(min_array.map(function (element) {
            return String(element);
          }))
        ]), 1);
        this.$refs.input_data_ref.table.loadData(this.merged_matrix);
        this.input_data = this.merged_matrix;

        // Input calculationの反映;
        // GHG Emissionを更新し、リストに格納
        var GHG = [];
        GHG = this.input_calculation[1].slice(1, col_num - 2).map(function (value, index) {
          return Emission(Number(this[1].slice(1, col_num - 2)[index]), Number(this[1].slice(col_num -
              2, col_num)[0]),
            Number(this[1].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);
        GHG = ["GHG emissions (Mt)"].concat(GHG.map(function (element) {
          return String(element);
        }));

        // Noxを更新し、リストに格納
        var Nox = [];
        Nox = this.input_calculation[2].slice(1, col_num - 2).map(function (value, index) {
          return Emission(Number(this[2].slice(1, col_num - 2)[index]), Number(this[2].slice(col_num -
              2, col_num)[0]),
            Number(this[2].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Nox = ["Annual mean NOx (ppm)"].concat(Nox.map(function (element) {
          return String(element);
        }));

        // PM10を更新し、リストに格納
        var PM10 = [];
        PM10 = this.input_calculation[3].slice(1, col_num - 2).map(function (value, index) {
          return Emission(Number(this[3].slice(1, col_num - 2)[index]), Number(this[3].slice(col_num -
              2, col_num)[0]),
            Number(this[3].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        PM10 = ["Annual mean PM10 (ug/m3)"].concat(PM10.map(function (element) {
          return String(element);
        }));

        // PM25を更新し、リストに格納
        var PM25 = [];
        PM25 = this.input_calculation[4].slice(1, col_num - 2).map(function (value, index) {
          return Emission(Number(this[4].slice(1, col_num - 2)[index]), Number(this[4].slice(col_num -
              2, col_num)[0]),
            Number(this[4].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        PM25 = ["Annual mean PM2.5 (ug/m3)"].concat(PM25.map(function (element) {
          return String(element);
        }));

        // Tecを更新し、リストに格納
        var Tec = [];
        Tec = this.input_calculation[5].slice(1, col_num - 2).map(function (value, index) {
          return Emission(Number(this[5].slice(1, col_num - 2)[index]), Number(this[5].slice(col_num -
              2, col_num)[0]),
            Number(this[5].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Tec = ["Total energy consumption"].concat(Tec.map(function (element) {
          return String(element);
        }));

        // Energy and Climateを更新し、リストに格納
        var EaC = [];
        EaC = this.input_calculation[6].slice(1, col_num - 2).map(function (value, index) {
          return Geomean([Number(GHG[index + 1]), Number(Nox[index + 1]), Number(PM10[index + 1]), Number(PM25[
            index + 1]), Number(Tec[index + 1])])
        }, GHG, Nox, PM10, PM25, Tec);

        EaC = ["Energy and Climate"].concat(EaC.map(function (element) {
          return String(element);
        }));

        // Clean water accessibility (%)を更新し、リストに格納
        var Cwa = [];
        Cwa = this.input_calculation[7].slice(1, col_num - 2).map(function (value, index) {
          return Emission2(Number(this[6].slice(1, col_num - 2)[index]), Number(this[6].slice(
              col_num - 2, col_num)[0]),
            Number(this[7].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Cwa = ["Clean water accessibility (%)"].concat(Cwa.map(function (element) {
          return String(element);
        }));

        // Public transportation system (km/km2)を更新し、リストに格納
        var Pts = [];
        Pts = this.input_calculation[8].slice(1, col_num - 2).map(function (value, index) {
          return Emission2(Number(this[7].slice(1, col_num - 2)[index]), Number(this[7].slice(
              col_num - 2, col_num)[0]),
            Number(this[7].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Pts = ["Public transportation system (km/km2)"].concat(Pts.map(function (element) {
          return String(element);
        }));

        // Waste collection and management (%)を更新し、リストに格納
        var Wcam = [];
        Wcam = this.input_calculation[9].slice(1, col_num - 2).map(function (value, index) {
          return Emission2(Number(this[8].slice(1, col_num - 2)[index]), Number(this[8].slice(
              col_num - 2, col_num)[0]),
            Number(this[8].slice(col_num - 2, col_num)[1]));
        }, this.merged_matrix);

        Wcam = ["Waste collection and management (%)"].concat(Wcam.map(function (element) {
          return String(element);
        }));

        // Green area per capita (square meter) を更新し、リストに格納
        var Gapc = [];
        Gapc = this.input_calculation[10].slice(1, col_num - 2).map(function (value, index) {
